export enum WSNetCmdType {
    WNCT_Request = "req",
    WNCT_Response = "res",
}

/**
 * @Description: 自定义cmd指令类型
 * @Date: 2024-06-11 17:16:15
 * @Author: GFanStudio-LeeSir-JYJ add
 * @return {*}
 */
export enum WSNetCmd {
    WNC_Register,
    WNC_Login,
    WNC_CheckConnect,
    WNC_GetUserInfo,
    WNC_UpdateUserInfo
}

/**
 * @Description: 自定义cmd请求和响应命令类型
 * @Date: 2024-06-11 17:15:55
 * @Author: GFanStudio-LeeSir-JYJ add
 * @return {*}
 */
export const WsNetCmdProtoId: Record<WSNetCmd, any> = {
    [WSNetCmd.WNC_Register]: {
        req: "ws.WNC_RegisterReq",
        res: "ws.WNC_RegisterRes",
    },
    [WSNetCmd.WNC_Login]: {
        req: "ws.WNC_LoginReq",
        res: "ws.WNC_LoginRes",
    },
    [WSNetCmd.WNC_CheckConnect]: {
        req: "ws.WNC_CheckConnectReq",
        res: "ws.WNC_CheckConnectRes",
    },
    [WSNetCmd.WNC_GetUserInfo]: {
        req: "ws.WNC_GetUserInfoReq",
        res: "ws.WNC_GetUserInfoRes",
    },
    [WSNetCmd.WNC_UpdateUserInfo]: {
        req: "ws.WNC_UpdateUserInfoReq",
        res: "ws.WNC_UpdateUserInfoRes",
    }
};

/**
 * @Description: 根据cmd命令类型获取对应的cmd指令
 * @Date: 2024-06-11 17:15:21
 * @Author: GFanStudio-LeeSir-JYJ add
 * @return {*}
 */
export const getGameProtoIdByWsNetCmd = (name: WSNetCmd,
    type: WSNetCmdType.WNCT_Request | WSNetCmdType.WNCT_Response) => {
    return WsNetCmdProtoId[name][type];
}