import { error } from "cc";
import { GFSingleClass } from "../base/GFSingleClass";
import { uiMgr } from "../../game/managers/UIManager";

enum RequestType {
    Type_Get = "GET",
    Type_Post = "POST",
}

let TAG = 'HttpManager';
class HttpManager extends GFSingleClass {

    public static ins(): HttpManager {
        return super.ins() as HttpManager;
    }

    /**
     * @Description: http的post请求
     * @Date: 2024-06-10 18:46:26
     * @Author: GFanStudio-LeeSir-JYJ add
     * @param {string} routeUrl
     * @param {any} params
     * @return {*}
     */
    public async httpPost(routeUrl: string, params: any) {
        return fetch(routeUrl, {
            method: RequestType.Type_Post,
            body: JSON.stringify(params),
            headers: {
                "Content-Type": "application/json",
            }
        }).then((response) => {
            return response.json();
        }).catch((error: Error) => {
            // uiMgr.showToastView(GameLanguageKey.GLK_NetError);
            console.log(TAG, `error ==> ${error}`);
        });
    }


}

export let httpMgr = HttpManager.ins();