import { _decorator, Component, EditBox, Node } from 'cc';
import { uiMgr } from '../../managers/UIManager';
import { GameUtils } from '../../utils/GameUtils';
import { NetCodeConst } from '../../common/GameConst';
import JSCrypto from "jsencrypt";
import { JsCrypt_PublicKey } from '../../../cores/crypto/SecretConfig';
import { httpMgr } from '../../../cores/manager/HttpManager';
import { GameHost } from '../../common/GameHost';
import { gameData } from '../../common/GameData';
import { GamePlayerData } from '../../common/GamePlayerData';
import { eventMgr } from '../../../cores/manager/EventManager';
import { GameEvents } from '../../common/GameEvents';

const { ccclass, property } = _decorator;

const crypt = new JSCrypto();
crypt.setKey(JsCrypt_PublicKey);


@ccclass('LoginView')
export class LoginView extends Component {

    @property(EditBox)
    uNameEditBox: EditBox = null;

    @property(EditBox)
    pwdEditBox: EditBox = null;

    @property(Node)
    netLoadingViewNode: Node = null;


    start() {
        this._initEvent();
        this._initUIData();
    }

    private _initUIData() {
        if (this.netLoadingViewNode) {
            this.netLoadingViewNode.active = false;
        }
    }

    private _initEvent() {

    }

    /**
     * @Description: 点击登录按钮
     * @Date: 2024-06-09 21:28:46
     * @Author: GFanStudio-LeeSir-JYJ add
     * @return {*}
     */
    public async onClickLoginBtnEvent() {

        if (!this.uNameEditBox ||
            !this.pwdEditBox ||
            GameUtils.isCheckStringEmpty(this.uNameEditBox.string) ||
            GameUtils.isCheckStringEmpty(this.pwdEditBox.string)) {
            // uiMgr.showToastView(GameLanguageKey.GLK_LoginOrRegisterErr.toString());
            return;
        }

        let originName = this.uNameEditBox.string;
        let originPwd = this.pwdEditBox.string;
        let uname = crypt.encrypt(originName);
        let password = crypt.encrypt(originPwd);
        const params = {
            uname,
            password
        };
        this._showNetLoadingView(true);
        const res = await httpMgr.httpPost(GameHost.gameUserLoginUrl, params);
        this._showNetLoadingView(false);
        if (!res) {
            return;
        }
        let code = res.code as number;
        let data = res.data;
        let message = res.message as string;
        if (code == NetCodeConst.LoginSuccess) {
            gameData.isOnlineLogin = true;
            let player = new GamePlayerData();
            player.uid = data.token;
            player.uuid = data.token;
            player.password = password as string;
            player.uname = uname as string;
            player.normalUName = originName;
            gameData.curPlayerData = player;
            uiMgr.showToastView(message);
            this.node.active = false;
            setTimeout(() => {
                eventMgr.emit(GameEvents.UI_LoginOrRegisterSuccess, { result: true, msg: message });
            }, 800);
        } else {
            uiMgr.showToastView(message);
        }

    }

    /**
     * @Description: 点击注册按钮
     * @Date: 2024-06-09 21:28:37
     * @Author: GFanStudio-LeeSir-JYJ add
     * @return {*}
     */
    public async onClickRegisterBtnEvent() {
        if (!this.uNameEditBox ||
            !this.pwdEditBox ||
            GameUtils.isCheckStringEmpty(this.uNameEditBox.string) ||
            GameUtils.isCheckStringEmpty(this.pwdEditBox.string)) {
            // uiMgr.showToastView(GameLanguageKey.GLK_LoginOrRegisterErr.toString());
            return;
        }

        let originName = this.uNameEditBox.string;
        let originPwd = this.pwdEditBox.string;
        let uname = crypt.encrypt(originName);
        let password = crypt.encrypt(originPwd);
        const params = {
            uname,
            password
        };
        this._showNetLoadingView(true);
        const res = await httpMgr.httpPost(GameHost.gameUserRegisterUrl, params);
        this._showNetLoadingView(false);
        if (!res) {
            return;
        }
        let code = res.code as number;
        let data = res.data;
        let message = res.message as string;
        if (code == NetCodeConst.RegisterSuccess) {
            gameData.isOnlineLogin = true;
            let player = new GamePlayerData();
            player.uid = data.token;
            player.uuid = data.token;
            player.password = password as string;
            player.uname = uname as string;
            player.normalUName = originName;
            gameData.curPlayerData = player;
            uiMgr.showToastView(message);
            this.node.active = false;
            setTimeout(() => {
                eventMgr.emit(GameEvents.UI_LoginOrRegisterSuccess, { result: true, msg: message });
            }, 800);
        } else {
            uiMgr.showToastView(message);
        }

    }

    private _showNetLoadingView(isShow: boolean) {
        if (this.netLoadingViewNode) {
            this.netLoadingViewNode.active = isShow;
        }
    }

    update(deltaTime: number) {

    }
}

