    public initShopWithData(shopTabType: GameShopTabType, itemObj: IFishConfigsObj | ICannonConfigsObj | IShopConfigsObj) {
        //解锁大炮、解锁鱼群、解锁银币兑换金币等
        this._curGameShopTabType = shopTabType;
        this._curShopPropsObj = itemObj;
        //设置背景图片
        loaderMgr.loadGameRes(GameResPath.getShopTabSpFramePath(shopTabType), SpriteFrame)
            .then((spriteFrame: SpriteFrame) => {
                if (this.bgSpr) {
                    this.bgSpr.spriteFrame = spriteFrame;
                }
            })
            .catch((err) => {
                console.error(TAG, `<initShopWithData> err : ${err}`);
            });
        let nameStr: string = "";
        let desStr: string = "";
        let sprAtlasName: string = "";
        let isNeedGold: boolean = true;
        let lockUseNum: number = 0;//解锁兑换道具需要的金币或银币数量
        let scaleItemValue: number = 1;
        if (this._curGameShopTabType == GameShopTabType.GST_Tab01) {
            let obj: ICannonConfigsObj = itemObj as ICannonConfigsObj;
            nameStr = obj.name;
            desStr = obj.desc.replace("$", `${obj.hurtHp}`);
            lockUseNum = obj.lockGolds;
            sprAtlasName = `${GameResPath.getCannonSpFramePath(obj.cannonType)}`;
        } else if (this._curGameShopTabType == GameShopTabType.GST_Tab02) {
            let obj: IFishConfigsObj = itemObj as IFishConfigsObj;
            nameStr = obj.name;
            desStr = obj.desc.replace("$", `${obj.golds}`);
            lockUseNum = obj.lockGolds;
            sprAtlasName = `${GameResPath.getFishSpFramePath(obj.fishType)}`;
            scaleItemValue = 0.5;
            if (FishTypeGroup.FTC_NormalFish == gameData.checkFishTypeGroupByType(obj.type)) {
                scaleItemValue = 1;
            }
        } else if (this._curGameShopTabType == GameShopTabType.GST_Tab03) {
            let obj: IShopConfigsObj = itemObj as IShopConfigsObj;
            nameStr = obj.name;
            desStr = obj.desc.replace("${1}", `${obj.num}`);
            desStr = desStr.replace("${2}", `${obj.golds}`);
            sprAtlasName = `${GameResPath.resGameTexturePath.gameTexture02}/${obj.icon}`;
            isNeedGold = false;
            lockUseNum = obj.num;
            scaleItemValue = 0.8;
        }
        if (this.iconGoldNode) {
            this.iconGoldNode.active = isNeedGold;
        }
        if (this.iconSilverNode) {
            this.iconSilverNode.active = !isNeedGold;
        }
        if (this.nameLabel) {
            this.nameLabel.string = nameStr;
        }
        if (this.desLabel) {
            this.desLabel.string = desStr;
        }
        if (this.numLabel) {
            this.numLabel.string = `${lockUseNum}`;
        }
        if (!GameUtils.isCheckStringEmpty(sprAtlasName)) {
            loaderMgr.loadGameRes(sprAtlasName, SpriteFrame)
                .then((spriteFrame: SpriteFrame) => {
                    if (this.itemSpr) {
                        this.itemSpr.spriteFrame = spriteFrame;
                        this.itemSpr.node.setScale(scaleItemValue, scaleItemValue, scaleItemValue);
                        this.itemSpr.node.active = true;
                    }
                })
                .catch((err) => {
                    console.error(TAG, `<initShopWithData> err : ${err}`);
                });
        }

    }

