import { GFSingleClass } from "../base/GFSingleClass";

let TAG = 'EventManager';
class EventManager extends GFSingleClass {

    private listeners: any = {};

    public static ins() {
        return super.ins() as EventManager;
    }

    /**
     * @Description: 发送订阅消息
     * @Date: 2024-04-21 13:12:11
     * @Author: GFanStudio-LeeSir-JYJ add
     * @param {any} eventName
     * @param {array} params
     * @return {*}
     */
    public emit(eventName: any, ...params: any[]): void {
        let cbs: any[] = this.listeners[`${eventName}`];
        if (!cbs) {
            return;
        }
        for (let i: number = 0, len: number = cbs.length; i < len; i += 2) {
            let cb: any = cbs[i];
            let host: any = cbs[i + 1];
            cb.call(host, ...params);
        }
    }

    /**
     * @Description:监听事件订阅消息 
     * @Date: 2024-04-21 13:12:21
     * @Author: GFanStudio-LeeSir-JYJ add
     * @param {any} eventName
     * @param {Function} cb
     * @param {any} host
     * @param {*} callNow
     * @param {array} params
     * @return {*}
     */
    public on(eventName: any, cb: Function, host: any = null, callNow = false, ...params: any[]): void {
        let cbs: any[] = this.listeners[`${eventName}`];
        if (!cbs) {
            this.listeners[eventName] = cbs = [];
        }
        cbs.push(cb, host);
        if (callNow) {
            cb.call(host, ...params);
        }
    }

    /**
     * @Description: 移除对应事件订阅
     * @Date: 2024-04-21 13:12:32
     * @Author: GFanStudio-LeeSir-JYJ add
     * @param {any} eventName
     * @param {Function} cb
     * @return {*}
     */
    public off(eventName: any, cb: Function) {
        let cbs: any[] = this.listeners[`${eventName}`];
        if (!cbs) {
            return;
        }
        let index: number = cbs.indexOf(cb);
        if (index < 0) {
            console.warn(TAG, `EventManager remove ${eventName} ,but cb not exit`);
        }
        cbs.splice(index, 2);
    }

    /**
     * @Description: 移除所有的事件订阅消息
     * @Date: 2024-04-21 13:12:43
     * @Author: GFanStudio-LeeSir-JYJ add
     * @return {*}
     */
    public removeAll() {
        for (let key in this.listeners) {
            this.listeners[key].length = 0;
        }
        this.listeners = {};
    }

}

let eventMgr = EventManager.ins();
export { eventMgr };