import { _decorator, Component, Label, Node } from 'cc';
import { GameUtils } from '../../utils/GameUtils';
const { ccclass, property } = _decorator;

@ccclass('ToastView')
export class ToastView extends Component {

    @property(Label)
    tipLabel: Label = null;
    private _timeout:number;
    start() {

    }

    /**
     * @Description: 对外开发的调用接口
     * @Date: 2024-06-10 09:08:05
     * @Author: GFanStudio-LeeSir-JYJ add
     * @param {string} tipContentStr 提示内容
     * @param {string} afterSecsClose 时间<多少毫秒后自动关闭提示界面>
     * @return {*}
     */
    public showTip(tipContentStr: string, afterSecsClose: number = 500) {
        this.node.active = true;
        if (this.tipLabel) {
            this.tipLabel.string = tipContentStr;
        }
        let self = this;
        this._timeout = setTimeout(() => {
            self._closeSelf();
        }, afterSecsClose);

    }

    /**
     * @Description: 自定义关闭函数
     * @Date: 2024-06-10 09:07:54
     * @Author: GFanStudio-LeeSir-JYJ add
     * @return {*}
     */
    private _closeSelf() {
        this.node.active = false;
        this.node.removeFromParent();
        this.node.destroy();
    }

    protected onDestroy(): void {
        clearTimeout(this._timeout);
        super.destroy();
    }

    update(deltaTime: number) {

    }
}

