export class GameResPath {

    /**bundle */
    public static fishBundleName = "fishGame";
    private static uiPrefabsRootPath = "prefabs/view/";
    /**UI界面 */
    public static uiItemsOrViews = {
        toastView: `${this.uiPrefabsRootPath}ToastView`,
        commonPopView: `${this.uiPrefabsRootPath}CommonPopView`,
        netLoadingView: `${this.uiPrefabsRootPath}NetLoadingView`,
        shopView: `${this.uiPrefabsRootPath}ShopView`,
    };

    /**配置表 */
    public static dataConfigsPath = {
        shopCfgs: "shopCfgs",
        cannonCfgs: "cannonCfgs",
        fishCfgs: "fishCfgs",
        traceCfgs: "traceCfgs",
        themeCfgs: "themeCfgs",
    };
    public static getLocalDataCfgPath(cfgFileName: string) {
        return `configs/${cfgFileName}`;
    }
    /**UI涉及的美术资源 */
    public static getThemeSpFramePath(bgImgFileName: string) {
        return `textures/bgs/${bgImgFileName}/spriteFrame`;
    }

    public static resGameTexturePath = {
        gameTexture01: "textures/plists/gameTextures01",
        gameTexture02: "textures/plists/gameTextures02",
    };

    /**
     * @Description: 根据鱼类类型名获取spriteFrame路径
     * @Date: 2024-06-13 18:18:57
     * @Author: GFanStudio-LeeSir-JYJ add
     * @param {string} fishTypeName
     * @param {boolean} isRunSp
     * @return {*}
     */
    public static getFishSpFramePath(fishTypeName: string, isRunSp: boolean = true) {
        // let fishRootPath: string = `textures/plists/gameTextures01/`;
        let fishRootPath: string = `${this.resGameTexturePath.gameTexture01}/`;
        if (isRunSp) {
            return (fishRootPath + fishTypeName + "_run_0");
        } else {
            return (fishRootPath + fishTypeName + "_dead_0");
        }
    }

    /**
     * @Description: 获取大炮的图片路径
     * @Date: 2024-06-14 19:03:23
     * @Author: GFanStudio-LeeSir-JYJ add
     * @param {number} cannonType
     * @return {*}
     */
    public static getCannonSpFramePath(cannonType: number) {
        let srcRootPath: string = `${this.resGameTexturePath.gameTexture01}/weapon_level_${cannonType}_0`;
        return srcRootPath;
    }

    /**
     * @Description:获取商店item道具背景
     * @Date: 2024-06-14 18:24:50
     * @Author: GFanStudio-LeeSir-JYJ add
     * @param {number} shopTabType
     * @return {*}
     */
    public static getShopTabSpFramePath(shopTabType: number) {
        let srcRootPath: string = `${this.resGameTexturePath.gameTexture02}/sp_shop_item_bg0${shopTabType}`;
        return srcRootPath;
    }

    /**音频音效 */
    private static audioRootPath = 'audios';
    public static gameAudioSfxPaths = {
        sfx_btn_lick: `${this.audioRootPath}/sfx_btn_lick`,
        sfx_cannonbomb01: `${this.audioRootPath}/sfx_cannonbomb01`,
        sfx_cannonbomb02: `${this.audioRootPath}/sfx_cannonbomb02`,
        sfx_castnet: `${this.audioRootPath}/sfx_castnet`,
        sfx_getgold: `${this.audioRootPath}/sfx_getgold`,
        sfx_redwarn: `${this.audioRootPath}/sfx_redwarn`,
        sfx_switchcannon: `${this.audioRootPath}/sfx_switchcannon`,
        sfx_welcome: `${this.audioRootPath}/sfx_welcome`,
    };


}