import { Vec3, path, view } from "cc";
import { GFSingleClass } from "../../cores/base/GFSingleClass";
import { GamePlayerData } from "./GamePlayerData";
import { ICannonConfigsObj, IFishConfigsObj, IShopConfigsObj, IThemeConfigsObj } from "./GameInterface";
import { localDBUtils } from "../../cores/utils/GFLocalDBUtils";
import { FishTypeGroup, GameLocalDBKey } from "./GameConst";
import { GameUtils } from "../utils/GameUtils";
import { WSManager } from "../../cores/net/WSManager";
import { WSNetCmd } from "../../cores/net/WSNetCmd";

let TAG = 'GameData';
class GameData extends GFSingleClass {

    public isLocalDebug: boolean = true;
    public isOnlineLogin: boolean = true;
    public isWSConnected: boolean = false;
    public static ins(): GameData {
        return super.ins() as GameData;
    }

    public curWeaponLevelType: number = 1;
    public curCannonNeedGoldsNum: number = 10;

    public curPlayerData: GamePlayerData = null;
    public cannonConfigsObjArr: ICannonConfigsObj[] = [];
    public fishConfigsObjArr: IFishConfigsObj[] = [];
    public shopConfigsObjArr: IShopConfigsObj[] = [];
    public traceConfigsObjArr: Vec3[][] = [];
    public themeConfigsObjArr: IThemeConfigsObj[] = [];
    /**
     * @Description: 转换鱼群路径
     * @Date: 2024-06-11 18:36:10
     * @Author: GFanStudio-LeeSir-JYJ add
     * @param {any} jsonObj
     * @param {number} traceCount
     * @return {*}
     */
    public parseTracePostionToArr(jsonObj: any, traceCount: number = 10) {
        if (!jsonObj) {
            return;
        }
        let visibleSize = view.getVisibleSize();
        let fixWidth = -5 - visibleSize.width / 2;
        this.traceConfigsObjArr = [];
        for (let i = 1; i <= 10; i++) {
            let tracePathName: string = `path0${i}`;
            let pathObj = jsonObj[tracePathName];
            if (!pathObj) {
                continue;
            }
            let oneTracePosArr: Vec3[] = [];
            let startPosObj = pathObj.start;
            if (startPosObj) {
                oneTracePosArr.push(new Vec3(fixWidth + startPosObj.x, startPosObj.y));
            }
            let centerPosObj = pathObj.center;
            if (centerPosObj) {
                oneTracePosArr.push(new Vec3(fixWidth + centerPosObj.x, centerPosObj.y));
            }
            let endPosObj = pathObj.end;
            if (endPosObj) {
                oneTracePosArr.push(new Vec3(fixWidth + endPosObj.x, endPosObj.y));
            }
            this.traceConfigsObjArr.push(oneTracePosArr);
        }
        // console.log(TAG, ` <parseTracePostionToArr> this.traceConfigsObjArr ==> ${this.traceConfigsObjArr}`);
    }

    /**
     * @Description: 转换游戏主题机制配置
     * @Date: 2024-06-12 14:46:14
     * @Author: GFanStudio-LeeSir-JYJ add
     * @param {any} jsonObj
     * @param {number} themeCount
     * @return {*}
     */
    public parseThemeCfgObjToArr(jsonObj: any, themeCount: number = 6) {
        if (!jsonObj) {
            return;
        }
        for (let i = 1; i <= 6; i++) {
            let levelName: string = `level${i}`;
            let themeObj = jsonObj[levelName];
            if (!themeObj) {
                continue;
            }
            let iThemeObj = <IThemeConfigsObj>themeObj;
            if (!iThemeObj) {
                continue;
            }
            this.themeConfigsObjArr.push(iThemeObj);
        }
    }

    /**
     * @Description: 根据鱼类型名获取对应配置
     * @Date: 2024-06-13 17:25:20
     * @Author: GFanStudio-LeeSir-JYJ add
     * @param {string} fishTypeName
     * @return {*}
     */
    public getFishConfigObjByFishTypeName(fishTypeName: string) {
        if (this.fishConfigsObjArr.length <= 0) {
            return null;
        }
        let targetObj: IFishConfigsObj = null;
        this.fishConfigsObjArr.forEach(fishCfgObj => {
            if (fishCfgObj.fishType == fishTypeName) {
                targetObj = fishCfgObj;
                return;
            }
        });
        return targetObj;
    }

    /**
     * @Description: 本地存档用户信息
     * @Date: 2024-06-11 18:35:57
     * @Author: GFanStudio-LeeSir-JYJ add
     * @return {*}
     */
    public updateLocalCacheUserInfo() {
        localDBUtils.setString(GameLocalDBKey.GLC_UserInfo, JSON.stringify(this.curPlayerData));
    }

    /**
     * @Description: 根据武器等级获取大炮对
     * @Date: 2024-06-14 09:22:43
     * @Author: GFanStudio-LeeSir-JYJ add
     * @param {number} weaponLevelType  武器等级<大炮/子弹/渔网>
     * @return {*}
     */
    public getWeaponHurtHpByWeaponLevelType(weaponLevelType: number): number {
        let hurtHp: number = 10;
        if (this.cannonConfigsObjArr.length <= 0) {
            return hurtHp;
        }
        let targetObj: ICannonConfigsObj = null;
        this.cannonConfigsObjArr.forEach(cfgObj => {
            if (cfgObj.cannonType == weaponLevelType) {
                targetObj = cfgObj;
                return;
            }
        });
        if (targetObj) {
            hurtHp = targetObj.hurtHp;
        }
        return hurtHp;
    }

    /**
     * @Description: 根据鱼群类型获取普通和鲨鱼类型名
     * @Date: 2024-06-14 09:38:00
     * @Author: GFanStudio-LeeSir-JYJ add
     * @param {FishTypeGroup} fishGroupType
     * @return {*}
     */
    public getSharkTypeNameByGroupType(fishGroupType: FishTypeGroup) {
        let sharkTypeName = "fish_shayu";
        if (fishGroupType == FishTypeGroup.FTC_SharkGold) {
            sharkTypeName = "fish_jinshayu";
        }
        return sharkTypeName;
    }

    public getNewSliverNum() {
        let silverNum = 0;
        if (!this.curPlayerData) {
            return silverNum;
        }
        silverNum = this.curPlayerData.silver_num;
        let localPlayerData = localDBUtils.getString(GameLocalDBKey.GLC_UserInfo);
        if (GameUtils.isCheckStringEmpty(localPlayerData)) {
            return silverNum;
        }
        let playerData: GamePlayerData = JSON.parse(localPlayerData);
        if (playerData) {
            this.curPlayerData = playerData;
            if (playerData.silver_num > silverNum) {
                silverNum = playerData.silver_num;
                this.curPlayerData.silver_num = playerData.silver_num;
            }
        }
        return silverNum;
    }

    /**
     * @Description: 根据武器等级获取消耗金币数
     * @Date: 2024-06-14 15:22:04
     * @Author: GFanStudio-LeeSir-JYJ add
     * @param {number} weaponLevelType
     * @return {*}
     */
    public getCannonUseGoldsNum(weaponLevelType: number) {
        let needGoldNum: number = 10;
        for (let i = 0; i < this.cannonConfigsObjArr.length; i++) {
            const element = this.cannonConfigsObjArr[i];
            if (element.cannonType == weaponLevelType) {
                needGoldNum = element.needGolds;
                break;
            }
        }
        return needGoldNum;
    }

    /**
     * @Description: 检查鱼属于那种鱼群
     * @Date: 2024-06-14 22:39:48
     * @Author: GFanStudio-LeeSir-JYJ add
     * @param {number} type
     * @return {*}
     */
    public checkFishTypeGroupByType(type: number) {
        if (type == 10) {
            return FishTypeGroup.FTC_SharkBlue;
        } else if (type == 11) {
            return FishTypeGroup.FTC_SharkGold;
        }
        return FishTypeGroup.FTC_NormalFish;

    }

    /**
     * @Description: 规则==>
     * 1、游戏中金币为最大购买道具,银币其次可以兑换金币
     * 2、银币控制游戏内部玩家计分累计和等级累积<分数和等级累积只针对现有或历史最大银币为准>
     * @Date: 2024-06-14 22:53:23
     * @Author: GFanStudio-LeeSir-JYJ add
     * @return {*}
     */
    public updateCheckUserScoreLevel() {
        if (!this.curPlayerData) {
            return 0;
        }
        if (this.curPlayerData.silver_num > this.curPlayerData.score_num) {
            this.curPlayerData.score_num = this.curPlayerData.silver_num;
        }
        for (let i = 0; i < this.themeConfigsObjArr.length; i++) {
            const themeObj = this.themeConfigsObjArr[i];
            if (!themeObj) {
                continue;
            }
            let min_score = themeObj.min_score;
            let max_score = themeObj.max_score;
            if (this.curPlayerData.score_num >= min_score && this.curPlayerData.score_num <= max_score) {
                //定时刷新等级机制
                gameData.curPlayerData.level = themeObj.level;
                break;
            }
        }
        this.updateLocalCacheUserInfo();
        return gameData.curPlayerData.level;
    }

    /**
     * @Description: 发送最新用户信息更新服务器
     * @Date: 2024-06-17 11:00:14
     * @Author: GFanStudio-LeeSir-JYJ add
     * @return {*}
     */
    public sendUserInfoToServer(callback?: Function) {
        if (!gameData || !gameData.isOnlineLogin || !gameData.isWSConnected) {
            if (callback) {
                callback();
            }
            return;
        }
        if (!gameData.curPlayerData) {
            return;
        }
        let uid = gameData.curPlayerData.uid;
        let uname = gameData.curPlayerData.normalUName;
        WSManager.sendMessage(WSNetCmd.WNC_UpdateUserInfo, { uid: uid, uname: uname, userinfo: gameData.curPlayerData });
    }

}

export let gameData = GameData.ins();


