import { Prefab, Vec3, instantiate, Node, find } from "cc";
import { GFSingleClass } from "../../cores/base/GFSingleClass";
import { GameResPath } from "../common/GameResPath";
import { loaderMgr } from "./LoaderManager";
import { CommonPopView } from "../ui/views/CommonPopView";
import { ToastView } from "../ui/views/ToastView";
import { GameUtils } from "../utils/GameUtils";
import { ShopView } from "../ui/views/ShopView";

let TAG = 'UIManager';
class UIManager extends GFSingleClass {

    public static ins(): UIManager {
        return super.ins() as UIManager;
    }

    /**
     * @Description: 展示通用弹框界面
     * @Date: 2024-06-10 10:49:23
     * @Author: GFanStudio-LeeSir-JYJ add
     * @param {string} contentStr
     * @param {string} titleStr
     * @param {string} btnDesStr
     * @param {Function} closeCallback
     * @param {Function} commonCallback
     * @param {Node} parentNode
     * @return {*}
     */
    public showCommonPopView(contentStr: string, titleStr?: string, btnDesStr?: string, closeCallback?: Function, commonCallback?: Function, parentNode?: Node) {

        loaderMgr.loadGameRes(GameResPath.uiItemsOrViews.commonPopView, Prefab)
            .then((prefab: Prefab) => {
                let popNode = instantiate(prefab);
                if (!popNode) {
                    return;
                }
                popNode.setScale(new Vec3(0.1, 0.1, 0.1));
                popNode.name = "CommonPopView";
                let comp = popNode.getComponent(CommonPopView);
                if (!comp) {
                    return;
                }
                comp.initWithData(contentStr, titleStr, btnDesStr, closeCallback, commonCallback);
                if (parentNode) {
                    popNode.parent = parentNode;
                } else {
                    popNode.parent = find('Canvas');
                }
            })
            .catch((err) => {
                console.error(TAG, `<showCommonPopView> err : ${err}`);
            });
    }

    /**
     * @Description: 显示通用气泡弹框
     * @Date: 2024-06-10 10:51:33
     * @Author: GFanStudio-LeeSir-JYJ add
     * @param {string} tipConentStr
     * @param {string} afterSecsClose 时间<多少毫秒后自动关闭提示界面>
     * @param {Node} parentNode
     * @return {*}
     */
    public showToastView(tipConentStr: string, afterSecsClose: number = 500, parentNode?: Node) {
        if (GameUtils.isCheckStringEmpty(tipConentStr)) {
            return;
        }
        loaderMgr.loadGameRes(GameResPath.uiItemsOrViews.toastView, Prefab)
            .then((prefab: Prefab) => {
                let popNode = instantiate(prefab);
                if (!popNode) {
                    return;
                }
                popNode.name = "ToastView";
                let comp = popNode.getComponent(ToastView);
                if (!comp) {
                    return;
                }
                comp.showTip(tipConentStr, afterSecsClose);
                if (parentNode) {
                    popNode.parent = parentNode;
                } else {
                    popNode.parent = find('Canvas');
                }
            })
            .catch((err) => {
                console.error(TAG, `<showToastView> err : ${err}`);
            });
    }

    /**
     * @Description: 打开商店
     * @Date: 2024-06-14 21:35:28
     * @Author: GFanStudio-LeeSir-JYJ add
     * @param {Node} parentNode
     * @return {*}
     */
    public showShopView(parentNode?: Node) {
        loaderMgr.loadGameRes(GameResPath.uiItemsOrViews.shopView, Prefab)
            .then((prefab: Prefab) => {
                let popNode = instantiate(prefab);
                if (!popNode) {
                    return;
                }
                popNode.name = "ShopView";
                let comp = popNode.getComponent(ShopView);
                if (!comp) {
                    return;
                }
                comp.updateShopViewWithData();
                if (parentNode) {
                    popNode.parent = parentNode;
                } else {
                    popNode.parent = find('Canvas');
                }
            })
            .catch((err) => {
                console.error(TAG, `<showToastView> err : ${err}`);
            });
    }

}

export let uiMgr = UIManager.ins();

