import { _decorator, Collider2D, Component, Contact2DType, IPhysics2DContact, Node } from 'cc';
import { GameColliderTags } from '../../common/GameConst';
const { ccclass, property } = _decorator;

@ccclass('NetItemCollider')
export class NetItemCollider extends Component {

    start() {
        let collider = this.node.getComponent(Collider2D);
        if (collider) {
            collider.on(Contact2DType.BEGIN_CONTACT, this.onBeginContact, this);
        }
    }

    private onBeginContact(selfCollider: Collider2D, otherCollider: Collider2D, contact: IPhysics2DContact | null) {

        if (selfCollider.tag == GameColliderTags.GCT_Net1 ||
            selfCollider.tag == GameColliderTags.GCT_Net2 ||
            selfCollider.tag == GameColliderTags.GCT_Net3 ||
            selfCollider.tag == GameColliderTags.GCT_Net4 ||
            selfCollider.tag == GameColliderTags.GCT_Net5 ||
            selfCollider.tag == GameColliderTags.GCT_Net6 ||
            selfCollider.tag == GameColliderTags.GCT_Net7
        ) {

            if (otherCollider.tag == GameColliderTags.GCT_Fish) {
                //TODO

            }

        }
    }

    update(deltaTime: number) {

    }
}

