import { _decorator, Component, game, Material, Node, Sprite, SpriteFrame } from 'cc';
import { gameData } from '../../common/GameData';
import { IThemeConfigsObj } from '../../common/GameInterface';
import { loaderMgr } from '../../managers/LoaderManager';
import { GameResPath } from '../../common/GameResPath';
import { uiMgr } from '../../managers/UIManager';
import { LanguageData } from 'db://assets/module_basic/scripts/language/LanguageData';
const { ccclass, property } = _decorator;

let TAG = 'BackgroundItem';
@ccclass('BackgroundItem')
export class BackgroundItem extends Component {

    @property(Sprite)
    bgNodeSp: Sprite = null;

    private _curLevel: number = -1;
    private _curScoreNum: number = -1;

    start() {
        this._initEvent();
        this._initUIData();
    }

    private _initEvent() {

    }

    private _initUIData() {
        if (!gameData || !gameData.curPlayerData) {
            return;
        }
    }

    private _onUpdateBackgroundTheme() {

        if (!this.bgNodeSp || !gameData || !gameData.curPlayerData) {
            return;
        }
        if (this._curLevel >= gameData.curPlayerData.level) {
            return;
        }
        // console.log(TAG, `<_onUpdateBackgroundTheme> level :${this._curLevel} , playerLevel:${gameData.curPlayerData.level} , themeConfigsObjArr: ${JSON.stringify(gameData.themeConfigsObjArr)}`);
        this._curLevel = gameData.curPlayerData.level;
        //等级提升提示
        if (this._curLevel > 1) {
            
            let levelUpTips = LanguageData.getLangByID("GLK_LevelUp",this._curLevel);
            uiMgr.showToastView(levelUpTips, 1500);
        }
        this._curScoreNum = gameData.curPlayerData.score_num;
        let curScore = gameData.curPlayerData.score_num;
        let level = gameData.curPlayerData.level;
        let findThemeObj: IThemeConfigsObj = null;
        for (let i = 0; i < gameData.themeConfigsObjArr.length; i++) {
            const themeObj = gameData.themeConfigsObjArr[i];
            if (!themeObj) {
                continue;
            }
            let min_score = themeObj.min_score;
            let max_score = themeObj.max_score;
            if (curScore >= min_score && curScore <= max_score) {
                findThemeObj = themeObj;
                //定时刷新等级机制
                gameData.curPlayerData.level = themeObj.level;
                break;
            }
        }
        if (findThemeObj) {
            let bg_name = findThemeObj.bg_name;
            loaderMgr.loadGameRes(GameResPath.getThemeSpFramePath(bg_name), SpriteFrame)
                .then((spriteFrame: SpriteFrame) => {
                    if (this.bgNodeSp) {
                        this.bgNodeSp.spriteFrame = spriteFrame;
                    }
                })
                .catch((err) => {
                    console.error(TAG, `<_onUpdateBackgroundTheme> err : ${err}`);
                });
        }
    }

    update(deltaTime: number) {
        this._onUpdateBackgroundTheme();
    }
}

