import { _decorator, Component, Node, tween, Vec3 } from 'cc';
import { NetNodePool } from '../../pools/NetNodePool';
const { ccclass, property } = _decorator;

@ccclass('NetItem')
export class NetItem extends Component {

    @property([Node])
    netNodeArr: Node[] = [];

    start() {

    }

    public playShotNetAni(weaponLevelType: number, callback?: Function) {
        let index = weaponLevelType;
        if (index > 7) {
            index = 7;
        } else if (index < 1) {
            index = 1;
        }
        for (let i = 0; i < this.netNodeArr.length; i++) {
            const node = this.netNodeArr[i];
            if (!node) {
                continue;
            }
            let netIndex = (i + 1);
            if (index == netIndex) {
                node.active = true;
                tween(node).to(0.6, { scale: new Vec3(0, 0) }).call(() => {
                    node.active = false;
                    node.setScale(1, 1, 1);
                    this.node.active = false;
                    this.node.removeFromParent();
                    this.node.parent = null;
                    // this.node.destroy();
                    NetNodePool.put(this.node);
                    if (callback) {
                        callback();
                    }
                    //TODO
                }).start();
            } else {
                node.active = false;
            }
        }

    }

    update(deltaTime: number) {

    }
}

