import { _decorator, Component, Node } from 'cc';
import { GameMgr } from 'db://assets/module_basic/scripts/GameMgr';
import { gameNet } from 'db://assets/module_basic/scripts/NetGameServer';
import { RoomMgr } from 'db://assets/module_basic/scripts/RoomMgr';
import { SubGameDef } from 'db://assets/module_basic/scripts/SubGameDef';
import { SubGameMgr } from 'db://assets/module_basic/scripts/SubGameMgr';
import { Bullet } from 'db://assets/module_basic/shared/protocols/public/fish/Fish';
import { MsgFishWave } from 'db://assets/module_basic/shared/protocols/public/fish/MsgFishWave';
import { MsgNewFish } from 'db://assets/module_basic/shared/protocols/public/fish/MsgNewFish';
import { MsgNewFishGroup } from 'db://assets/module_basic/shared/protocols/public/fish/MsgNewFishGroup';
import { MsgRoomFish } from 'db://assets/module_basic/shared/protocols/public/fish/MsgRoomFish';
import { ModuleDef } from 'db://assets/scripts/ModuleDef';
import { gameData } from './common/GameData';
import { GamePlayerData } from './common/GamePlayerData';
import { UserMgr } from 'db://assets/module_basic/scripts/UserMgr';
import { GameResPath } from './common/GameResPath';
import { loaderMgr } from './managers/LoaderManager';
import { JsonAsset } from 'cc';
import { ICannonConfigsObj, IFishConfigsObj, IShopConfigsObj } from './common/GameInterface';
import { ResFishConfig } from 'db://assets/module_basic/shared/protocols/public/fish/PtlFishConfig';
import { ApiReturn } from 'tsrpc-browser';
import { assetManager } from 'cc';
const { ccclass, property } = _decorator;

@ccclass('FishGameMgr')
export class FishGameMgr extends GameMgr {
    private static _inst: FishGameMgr;
    private _fishList: Array<MsgNewFish>; //鱼
    private _fishGroup:MsgNewFishGroup;
    private _getData:boolean;
    private _bulletList:Array<Bullet>;
    private _lastBulletId:number;
    //从服务器获取的配置信息
    public fishConfig:ResFishConfig;
    public static get inst(): FishGameMgr {
        if (!this._inst) {
            this._inst = new FishGameMgr();
        }
        return this._inst;
    }

    constructor() {
        super();
        this._gameType = SubGameDef.FISH_MASTER;
        //this._entryScene = { name: 'loading_fish', bundle: ModuleDef.FISH };
        this._gameScene = { name: 'game_fish', bundle: ModuleDef.FISH };
        this._loadingScene = { name: 'subgame_loading', bundle: ModuleDef.BASIC }
    }

   override init() {
        this._fishList = [];
        this._bulletList = [];
        this._lastBulletId = 0;
        gameNet.listenMsg("fish/NewFish", this.onNet_NewFish, this);
        gameNet.listenMsg("fish/RoomFish", this.onNet_RoomFish, this);
        gameNet.listenMsg("fish/FishWave", this.onNet_FishWave, this);
        gameNet.listenMsg("fish/NewFishGroup", this.onNet_FishGroup, this);
    }
    override uinit() {
        this._getData = false;
        gameNet.unlistenMsgAllByThis(this);
    }
    /**获取数据 获取过数据后 清空 为了场景没加载好，漏掉消息*/
    public getInitData():any[]
    {
        this._getData = true;
        var result:any[] = [this._fishList, this._fishGroup,this._bulletList,this._lastBulletId];
        return result;
    }


    private onNet_NewFish(msg:MsgNewFish)
    {
        if(this._getData)
        {
            return;
        }
        this._fishList.push(msg);
    }
    private onNet_RoomFish(msg:MsgRoomFish)
    {
        if(this._getData)
        {
            return;
        }
        for(var i:number = 0;i<msg.fishes.length;i++)
        {
            this.onNet_NewFish(msg.fishes[i]);
        }
        if(msg.bullets)
        {
            for(var j:number = 0;j<msg.bullets.length;j++)
            {
                this._bulletList.push(msg.bullets[j]);
            }
        }
        if(msg.lastBulletId)
        {
            this._lastBulletId = Number(msg.lastBulletId);
        }
        
    }
    private onNet_FishWave(msg:MsgFishWave)
    {
        if(this._getData)
        {
            return;
        }
        this._fishGroup = null;
        this._fishList = [];
    }
    private onNet_FishGroup(msg:MsgNewFishGroup)
    {
        if(this._getData)
        {
            return;
        }
        this._fishGroup = msg;
    }
    /**
     * 加载资源
     */
    public override async preLoadRes(processFun:Function):Promise<boolean> {
        await this.loadResource(processFun);
        await this.preLoadGameScene(processFun);
        processFun(1);
        return true;
    }
    private async preLoadGameScene(processFun:Function) {
        return new Promise((resolve, reject) => {
            let bundle = assetManager.getBundle(ModuleDef.FISH);
            if(bundle)
            {
                bundle.preloadScene(SubGameMgr.gameMgr.gameScene.name,null,function ()
                {
                    resolve(true);
                }.bind(this));  //7
                
            }
            else
            {
                resolve(false);
            }
        });
        
    }

    public async loadResource(processFun:Function):Promise<boolean>
    {
        return new Promise((resolve, reject) => {
            let cfgsPathArr = [
                GameResPath.dataConfigsPath.cannonCfgs,
                GameResPath.dataConfigsPath.fishCfgs,
                GameResPath.dataConfigsPath.shopCfgs,
                GameResPath.dataConfigsPath.traceCfgs,
                GameResPath.dataConfigsPath.themeCfgs
            ];
            var curCount:number = 0;
            for (let i = 0; i < cfgsPathArr.length; i++) {
                const pathName = cfgsPathArr[i];
                loaderMgr.loadGameRes(GameResPath.getLocalDataCfgPath(pathName), JsonAsset)
                    .then((jsonAsset: JsonAsset) => {
    
                        if (pathName == GameResPath.dataConfigsPath.cannonCfgs) {
                            gameData.cannonConfigsObjArr = <ICannonConfigsObj[]>jsonAsset.json;
                        } else if (pathName == GameResPath.dataConfigsPath.fishCfgs) {
                            gameData.fishConfigsObjArr = <IFishConfigsObj[]>jsonAsset.json;
                        } else if (pathName == GameResPath.dataConfigsPath.shopCfgs) {
                            gameData.shopConfigsObjArr = <IShopConfigsObj[]>jsonAsset.json;
                        } else if (pathName == GameResPath.dataConfigsPath.traceCfgs) {
                            gameData.parseTracePostionToArr(jsonAsset.json);
                        } else if (pathName == GameResPath.dataConfigsPath.themeCfgs) {
                            gameData.parseThemeCfgObjToArr(jsonAsset.json);
                        }
                        curCount++;
                        if(processFun)
                        {
                            processFun(curCount/(cfgsPathArr.length+1))
                        }
                        if (curCount >= cfgsPathArr.length) {
                            this.resReady();
                            
                            resolve(true);
                        }
                    })
                    .catch((err) => {
                        console.error(`loading <_initUIData> err : ${err}`);
                        reject(false);
                    });
    
            }
        });
    }
    public resReady(): void {
        if (gameData.isLocalDebug) {
            gameData.isOnlineLogin = true;
            gameData.isWSConnected = true;
            let player = new GamePlayerData();
            player.uid = "test";
            player.uuid = "test";
            player.password = "test";
            player.uname = "test";
            player.normalUName = "test";
            player.score_num = 0;
            player.silver_num = 10000;
            player.gold_num = UserMgr.inst.coin;
            player.level = 10;
            gameData.curPlayerData = player;
            gameData.updateLocalCacheUserInfo();//test
        }
    }

    /**
     * 请求服务器配置
     * @returns 
     */
    public async requestFishConfig():Promise<boolean>
    {
            let cfg:ApiReturn<ResFishConfig> = await gameNet.callApi("fish/FishConfig", {});
            this.fishConfig = cfg.isSucc?cfg.res:null;
            return true;
    }
    /** 
     * 获取子弹的金币消耗
    */
    public getBulletCoin(bulletLev:number):number
    {
        if(this.fishConfig)
        {
            for(var i:number = 0;i<this.fishConfig.bullets.length;i++)
            {
                if(this.fishConfig.bullets[i].bulletLevel == bulletLev)
                {
                    return this.fishConfig.bullets[i].needGolds;
                }
            }
        }
        return -1;
    }
}

SubGameMgr.inst.registerGameMgr(FishGameMgr.inst);


