import { _decorator, Component, Node, tween, Tween } from 'cc';
const { ccclass, property } = _decorator;

@ccclass('NetLoadingView')
export class NetLoadingView extends Component {

    @property(Node)
    loadingNode: Node = null;

    start() {
        this._initUIData();
    }

    private _initUIData() {

        if (this.loadingNode) {
            Tween.stopAllByTarget(this.loadingNode);
            this.scheduleOnce(() => {
                if (this.loadingNode) {
                    tween(this.loadingNode).by(0.5, { angle: 360 }).repeatForever().start();
                }
            }, 0.1);
        }

    }

    /**
     * @Description: 自定义关闭函数
     * @Date: 2024-06-10 09:07:54
     * @Author: GFanStudio-LeeSir-JYJ add
     * @return {*}
     */
    private _closeSelf() {
        if (this.loadingNode) {
            Tween.stopAllByTarget(this.loadingNode);
        }
        this.node.active = false;
        this.node.removeFromParent();
        this.node.destroy();
    }

    // protected onDestroy(): void {
    //     this._closeSelf();
    //     super.onDestroy();
    // }

    update(deltaTime: number) {

    }

}

