import { Asset, AssetManager, assetManager, error } from "cc";
import { GFSingleClass } from "../../cores/base/GFSingleClass";
import { GameResPath } from "../common/GameResPath";
import { GameUtils } from "../utils/GameUtils";
import { ModuleDef } from 'db://assets/scripts/ModuleDef';
import { ResourceMgr } from 'db://assets/core_tgx/base/ResourceMgr';

let TAG = 'LoaderManager';
export class LoaderManager extends GFSingleClass {

    public static ins(): LoaderManager {
        return super.ins() as LoaderManager;
    }

    /**
     * @Description: 加载游戏bundle
     * @Date: 2024-06-10 10:02:36
     * @Author: GFanStudio-LeeSir-JYJ add
     * @param {string} bundleNameOrPath
     * @return {*}
     */
    public loadBundle(bundleNameOrPath: string = GameResPath.fishBundleName) {
        return new Promise((reslove, reject) => {
            if (GameUtils.isCheckStringEmpty(bundleNameOrPath)) {
                return reject(new Error(`${bundleNameOrPath} is empty`));
            }
            assetManager.loadBundle(bundleNameOrPath, (err: Error, bundle: AssetManager.Bundle) => {
                if (err) {
                    console.error(TAG, `load bundle <${GameResPath.fishBundleName}> error: ${err}`);
                    return reject(err);
                }
                return reslove(bundle);
            });
        });
    }

    /**
     * @Description: 通用根据资源类型加载bundle中游戏资源
     * @Date: 2024-06-10 10:01:38
     * @Author: GFanStudio-LeeSir-JYJ add
     * @param {string} resPath 资源路径
     * @param {*} resType 资源类型
     * @return {*}
     */
    public loadGameRes(resPath: string, resType):Promise<any> {
        return new Promise((reslove, reject) => {
            ResourceMgr.inst.loadResByModuleBundle(ModuleDef.FISH, "fishGame/"+resPath, resType, (error, resObj: Asset) => {
                if (error || !resObj) {
                    return reject(error);
                }
                return reslove(resObj);
            });
        });
    }

}

export let loaderMgr = LoaderManager.ins();