import { ITweenOption, SceneAsset, TweenEasing, Vec3, director, tween, v3, Node, AudioClip, find, AudioSource } from "cc";
import { GameSceneName } from "../common/GameConst";
import { WSManager } from "../../cores/net/WSManager";

let TAG = 'GameUtils';
export class GameUtils {


    /**
     * @Description: 切换场景
     * @Date: 2024-06-10 09:12:16
     * @Author: GFanStudio-LeeSir-JYJ add
     * @param {SceneNames} sceneName 场景名
     * @param {boolean} isPreLoad 是否提前预加载场景
     * @return {*}
     */
    public static switchScene(sceneName: GameSceneName, isPreLoad: boolean = false) {
        //标记当前是否正在切换场景过程中
        WSManager.setChangeScene(true);
        if (isPreLoad) {
            director.preloadScene(sceneName, (error: Error, scene: SceneAsset) => {
                if (error) {
                    console.log(TAG, `<switchScene> error ==> ${error}`);
                    return;
                }
                director.loadScene(sceneName, () => {
                    //标记当前是否正在切换场景过程中
                    WSManager.setChangeScene(false);
                });
            });
        } else {
            director.loadScene(sceneName, () => {
                //标记当前是否正在切换场景过程中
                WSManager.setChangeScene(false);
            });
        }
    }

    /**
     * @Description: 判断字符串是否为空
     * @Date: 2024-04-20 22:56:15
     * @Author: GFanStudio-LeeSir-JYJ add
     * @param {string} valueStr
     * @return {*}
     */
    public static isCheckStringEmpty(valueStr: string) {

        if (valueStr == null || valueStr == undefined || valueStr == "" || this.isEmptyStr(valueStr)) {
            return true;
        }
        return false;

    }

    /**
     * @Description: 判断字符串是否包含空格或空白
     * @Date: 2024-04-20 22:56:26
     * @Author: GFanStudio-LeeSir-JYJ add
     * @param {string} valueStr
     * @return {*}
     */
    private static isEmptyStr(valueStr: string): boolean {
        if ((/^\s*$/.test(valueStr)) || (valueStr.trim().length == 0)) {
            return true;
        }
        return false;
    }

    /**
     * @Description: 递归过滤随机下标方法
     * @Date: 2024-06-13 17:33:45
     * @Author: GFanStudio-LeeSir-JYJ add
     * @param {number} totalCount 数组的总长
     * @param {number} filterNumArr 要过滤掉的index
     * @return {*}
     */
    public static recursionRandomFunc(totalCount: number, filterNumArr: number[] = [9, 10]) {
        let randomFishIndex = Math.floor(Math.random() * totalCount);
        if (filterNumArr.indexOf(randomFishIndex) != -1) {
            return this.recursionRandomFunc(totalCount, filterNumArr);
        }
        return randomFishIndex;
    }


    /**
     * @Description: 自定义贝塞尔缓动动画
     * @Date: 2024-06-13 21:21:41
     * @Author: GFanStudio-LeeSir-JYJ add
     * @param {Node} actionNode
     * @param {number} duration
     * @param {Vec3} startPos
     * @param {Vec3} centertPos
     * @param {Vec3} endPos
     * @param {TweenEasing} tweenEasing
     * @param {ITweenOption} option
     * @param {number} scaleValue
     * @return {*}
     */
    public static qtCustomBezierToAction(actionNode: Node, duration: number, startPos: Vec3, centertPos: Vec3, endPos: Vec3, tweenEasing: TweenEasing, option?: ITweenOption, scaleValue?: number) {
        const bezierTween = tween(actionNode);
        let twoBezier = (t: number, p1: Vec3, cp: Vec3, p2: Vec3) => {
            let x = (1 - t) * (1 - t) * p1.x + 2 * t * (1 - t) * cp.x + t * t * p2.x;
            let y = (1 - t) * (1 - t) * p1.y + 2 * t * (1 - t) * cp.y + t * t * p2.y;
            return v3(x, y, 0);
        };
        const scaleTween = tween()
            .to(duration, { scale: new Vec3((scaleValue ? scaleValue : 1), (scaleValue ? scaleValue : 1), 0) });
        const mainTween = tween()
            .to(duration, {}, {
                onStart: (target: Node) => {
                    option.onStart?.();
                },
                onUpdate: (target: Node, ratio) => {
                    if (target) {
                        target.position = twoBezier(ratio, startPos, centertPos, endPos)
                    }
                },
                onComplete: (target: Node) => {
                    option.onComplete?.(target);
                },
                easing: tweenEasing
            });
        return bezierTween.parallel(scaleTween, mainTween);
    }

    /**
     * @Description: 数字固定长度不够补0
     * @Date: 2024-06-14 11:29:54
     * @Author: GFanStudio-LeeSir-JYJ add
     * @param {number} num
     * @param {number} length
     * @return {*}
     */
    public static prefixInteger(num: number, length: number) {
        return (Array(length).join('0') + num).slice(-length);
    }

    /**
     * @Description: 游戏场景播放音效播放音效
     * @Date: 2024-06-16 16:37:27
     * @Author: GFanStudio-LeeSir-JYJ add
     * @return {*}
     */
    public static playSfx(sfxClip: AudioClip) {
        if (!sfxClip) {
            return;
        }
        let bgAudioNode = find('Canvas').getChildByName('bgAudioNode');
        if (bgAudioNode) {
            let audioSourceComp = bgAudioNode.getComponent(AudioSource);
            if (audioSourceComp) {
                audioSourceComp.playOneShot(sfxClip);
            }
        }
    }

}