import { NodePool, Prefab, Node, instantiate, Tween } from "cc"

export class FishNodePool {

    private static _nodePool: NodePool = new NodePool();

    /**
     * @Description: 首次初始化一定数量的节点备用
     * @Date: 2024-06-13 22:53:58
     * @Author: GFanStudio-LeeSir-JYJ add
     * @param {Prefab} prefab
     * @return {*}
     */
    public static preInitPool(prefab: Prefab = null) {
        if (!prefab) {
            return;
        }
        for (let i = 0; i < 20; i++) {
            let node: Node = null;
            if (prefab) {
                node = instantiate(prefab);
            }
            this.put(node);
        }
    }

    /**
     * @Description: 从节点池取出node
     * @Date: 2024-06-13 22:53:44
     * @Author: GFanStudio-LeeSir-JYJ add
     * @param {any} prefabNode
     * @return {*}
     */
    public static get(prefabNode: any): Node {
        let node: Node = null;
        if (this._nodePool.size() > 0) {
            node = this._nodePool.get();
        } else {
            node = instantiate(prefabNode);
        }
        node.active = true;
        return node;
    }

    /**
     * @Description: 移除对象重新放入到节点池等待使用
     * @Date: 2024-06-13 22:53:20
     * @Author: GFanStudio-LeeSir-JYJ add
     * @param {Node} node
     * @return {*}
     */
    public static put(node: Node) {
        if (!node) {
            return;
        }
        Tween.stopAllByTarget(node);
        node.active = false;
        node.parent = null;
        this._nodePool.put(node);
    }

    /**
     * @Description: 清空对象池
     * @Date: 2024-06-13 22:53:08
     * @Author: GFanStudio-LeeSir-JYJ add
     * @return {*}
     */
    public static clearNodePool() {
        this._nodePool.clear();
    }

}
