import CryptoES from "crypto-es";
import CryptoJS from "./crypto-js.min.js";
import { Crypto_AESSecretKey } from "./SecretConfig";


export class CryptoUtils {

    private static _cryptoKey: string = Crypto_AESSecretKey;//自定义<初始化加密库使用>
    private static _cryptoIV: CryptoES.lib.WordArray = CryptoES.enc.Hex.parse('aes_iv');//自定义<初始化加密库使用>

    /**
     * @Description: 简单aes加密
     * @Date: 2024-04-25 13:37:52
     * @Author: GFanStudio-LeeSir-JYJ add
     * @param {string} enCodeStr
     * @return {*}
     */
    public static easyAES_encrypt(enCodeStr: string) {
        return CryptoES.AES.encrypt(enCodeStr, this._cryptoKey).toString();
    }

    /**
     * @Description: 简单aes解密
     * @Date: 2024-04-25 13:38:04
     * @Author: GFanStudio-LeeSir-JYJ add
     * @param {string} deCodeStr
     * @return {*}
     */
    public static easyAES_decrypt(deCodeStr: string) {
        return CryptoES.AES.decrypt(deCodeStr, this._cryptoKey).toString(CryptoES.enc.Utf8);
    }

    /**
     * @Description: md5加密
     * @Date: 2024-04-25 13:37:42
     * @Author: GFanStudio-LeeSir-JYJ add
     * @param {string} conentStr
     * @return {*}
     */
    public static easyMD5(conentStr: string) {
        return CryptoES.MD5(conentStr).toString();
    }

    /**
     * @Description:复杂AES加密 
     * @Date: 2024-04-25 13:35:23
     * @Author: GFanStudio-LeeSir-JYJ add
     * @param {string} msg
     * @param {string} key
     * @param {string} iv
     * @return {*}
     */
    public static complex_AES_Encrypt(msg: string, key?: string, iv?: string): string {
        return CryptoES.AES.encrypt(
            msg,
            this._cryptoKey,
            {
                iv: this._cryptoIV,
                format: this.JsonFormatter
            },
        ).toString();
    }

    /**
     * @Description: 复杂AES解密
     * @Date: 2024-04-25 13:35:10
     * @Author: GFanStudio-LeeSir-JYJ add
     * @param {string} str
     * @param {string} key
     * @param {string} iv
     * @return {*}
     */
    public static complex_AES_Decrypt(str: string, key?: string, iv?: string): string {
        const decrypted = CryptoES.AES.decrypt(
            str,
            this._cryptoKey,
            {
                iv: this._cryptoIV,
                format: this.JsonFormatter
            },
        );
        return decrypted.toString(CryptoES.enc.Utf8);
    }

    /**
     * @Description: 自定义一个json格式化
     * @Date: 2024-04-25 13:37:18
     * @Author: GFanStudio-LeeSir-JYJ add
     * @return {*}
     */
    private static JsonFormatter = {
        stringify: function (cipherParams: any) {
            const jsonObj: any = { ct: cipherParams.ciphertext.toString(CryptoES.enc.Base64) };
            if (cipherParams.iv) {
                jsonObj.iv = cipherParams.iv.toString();
            }
            if (cipherParams.salt) {
                jsonObj.s = cipherParams.salt.toString();
            }
            return JSON.stringify(jsonObj);
        },
        parse: function (jsonStr: any) {
            const jsonObj = JSON.parse(jsonStr);
            const cipherParams = CryptoES.lib.CipherParams.create(
                { ciphertext: CryptoES.enc.Base64.parse(jsonObj.ct) },
            );
            if (jsonObj.iv) {
                cipherParams.iv = CryptoES.enc.Hex.parse(jsonObj.iv)
            }
            if (jsonObj.s) {
                cipherParams.salt = CryptoES.enc.Hex.parse(jsonObj.s)
            }
            return cipherParams;
        },
    };

    /**
     * @Description: 加密-对称加密算法
     * @Date: 2024-04-25 11:38:49
     * @Author: GFanStudio-LeeSir-JYJ add
     * @param {string} enCodeStr
     * @return {*}
     */
    public static encrypt(enCodeStr: string): string {
        return CryptoJS.AES.encrypt(enCodeStr, Crypto_AESSecretKey).toString();
    }

    /**
     * @Description: 解密-对称加密算法
     * @Date: 2024-04-25 11:38:49
     * @Author: GFanStudio-LeeSir-JYJ add
     * @param {string} enCodeStr
     * @return {*}
     */
    public static decrypt(deCodeStr: string): string {
        return CryptoJS.AES.decrypt(deCodeStr, Crypto_AESSecretKey).toString(CryptoJS.enc.Utf8);
    }

}

