import { _decorator, AudioClip, AudioSource, Component, director, find, game, instantiate, Node, Prefab, ScrollView, Toggle } from 'cc';
import { GameShopTabType } from '../../common/GameConst';
import { gameData } from '../../common/GameData';
import { ShopItem } from '../items/ShopItem';
import { ICannonConfigsObj, IFishConfigsObj } from '../../common/GameInterface';
import { GameEvents } from '../../common/GameEvents';
import { gameMgr } from '../../gameMgr';
import { GameUtils } from '../../utils/GameUtils';
const { ccclass, property } = _decorator;

@ccclass('ShopView')
export class ShopView extends Component {

    @property(ScrollView)
    shopScrollView: ScrollView = null;

    @property(Prefab)
    shopItemPrefab: Prefab = null;

    @property(Node)
    emptySpNode: Node = null;

    @property(AudioClip)
    clickSfxClip: AudioClip = null;

    private _curSelectTabType: GameShopTabType = GameShopTabType.GST_Tab01;

    start() {
        this._initEvent();
        this._initUIData();
    }

    private _initEvent() {
        director.on(GameEvents.UI_ShopExchangeRefreshUI, this._onShopExchangeRefreshUICallback, this);
    }

    private _initUIData() {
        this._updateEmptyNodeVisible(false);
    }

    private _updateEmptyNodeVisible(value: boolean) {
        if (this.emptySpNode) {
            this.emptySpNode.active = value;
        }
    }

    updateShopViewWithData() {
        if (!this.shopScrollView || !this.shopItemPrefab || !gameData || !gameData.curPlayerData) {
            return;
        }
        this._updateEmptyNodeVisible(false);
        this.shopScrollView.content.removeAllChildren();
        if (this._curSelectTabType === GameShopTabType.GST_Tab01) {
            let lockPropsTypeArr = gameData.curPlayerData.lock_cannontype;
            if (lockPropsTypeArr.length >= gameData.cannonConfigsObjArr.length) {
                this._updateEmptyNodeVisible(true);
                return;
            }
            let unLockObjArr: ICannonConfigsObj[] = [];
            for (let i = 0; i < gameData.cannonConfigsObjArr.length; i++) {
                const element = gameData.cannonConfigsObjArr[i];
                if (lockPropsTypeArr.indexOf(element.cannonType) == -1) {
                    unLockObjArr.push(element);
                }
            }
            for (let i = 0; i < unLockObjArr.length; i++) {
                const element = unLockObjArr[i];
                let shopItemNode = instantiate(this.shopItemPrefab);
                if (!shopItemNode) {
                    continue;
                }
                let shopItemComp = shopItemNode.getComponent(ShopItem);
                if (!shopItemComp) {
                    continue;
                }
                shopItemComp.initShopWithData(this._curSelectTabType, element);
                shopItemNode.parent = this.shopScrollView.content;
            }

        } else if (this._curSelectTabType === GameShopTabType.GST_Tab02) {
            let lockPropsTypeArr = gameData.curPlayerData.lock_fishtype;
            if (lockPropsTypeArr.length >= gameData.fishConfigsObjArr.length) {
                this._updateEmptyNodeVisible(true);
                return;
            }
            let unLockObjArr: IFishConfigsObj[] = [];
            for (let i = 0; i < gameData.fishConfigsObjArr.length; i++) {
                const element = gameData.fishConfigsObjArr[i];
                if (lockPropsTypeArr.indexOf(element.type) == -1) {
                    unLockObjArr.push(element);
                }
            }
            for (let i = 0; i < unLockObjArr.length; i++) {
                const element = unLockObjArr[i];
                let shopItemNode = instantiate(this.shopItemPrefab);
                if (!shopItemNode) {
                    continue;
                }
                let shopItemComp = shopItemNode.getComponent(ShopItem);
                if (!shopItemComp) {
                    continue;
                }
                shopItemComp.initShopWithData(this._curSelectTabType, element);
                shopItemNode.parent = this.shopScrollView.content;
            }

        } else if (this._curSelectTabType === GameShopTabType.GST_Tab03) {
            let lockPropsTypeArr = gameData.shopConfigsObjArr;
            for (let i = 0; i < lockPropsTypeArr.length; i++) {
                const element = lockPropsTypeArr[i];
                let shopItemNode = instantiate(this.shopItemPrefab);
                if (!shopItemNode) {
                    continue;
                }
                let shopItemComp = shopItemNode.getComponent(ShopItem);
                if (!shopItemComp) {
                    continue;
                }
                shopItemComp.initShopWithData(this._curSelectTabType, element);
                shopItemNode.parent = this.shopScrollView.content;
            }


        }

    }

    onToggleGroupSingleEvent(toggle: Toggle, customEventData: any) {
        
        if (toggle.isChecked) {
            GameUtils.playSfx(this.clickSfxClip);
            if ("1" == customEventData) {
                this._curSelectTabType = GameShopTabType.GST_Tab01;
            } else if ("2" == customEventData) {
                this._curSelectTabType = GameShopTabType.GST_Tab02;
            } else if ("3" == customEventData) {
                this._curSelectTabType = GameShopTabType.GST_Tab03;
            }
            this.updateShopViewWithData();
        }

    }

    /**
     * @Description:商店购买后刷新界面逻辑
     * @Date: 2024-06-14 21:52:10
     * @Author: GFanStudio-LeeSir-JYJ add
     * @return {*}
     */
    private _onShopExchangeRefreshUICallback() {
        this.updateShopViewWithData();
    }

    onClickCloseBtnEvent() {
        GameUtils.playSfx(this.clickSfxClip);
        this.node.active = false;
        this.node.removeFromParent();
        this.node.parent = null;
        this.node.destroy();
    }

    protected onDestroy(): void {
        director.off(GameEvents.UI_ShopExchangeRefreshUI, this._onShopExchangeRefreshUICallback, this);
    }

    update(deltaTime: number) {

    }
}

