import { sys } from "cc";
import { GFSingleClass } from "../base/GFSingleClass";

let TAG = 'GFLocalDBUtils';
class GFLocalDBUtils extends GFSingleClass {

    public static ins(): GFLocalDBUtils {
        return super.ins() as GFLocalDBUtils;
    }

    /**
     * @Description: 存储字符串
     * @Date: 2024-06-11 18:18:11
     * @Author: GFanStudio-LeeSir-JYJ add
     * @param {*} key
     * @param {*} value
     * @return {*}
     */
    public setString(key, value) {
        this._writeToLocal(key, value);
    }

    /**
     * @Description: 获取字符串
     * @Date: 2024-06-11 18:18:22
     * @Author: GFanStudio-LeeSir-JYJ add
     * @param {*} key
     * @return {*}
     */
    public getString(key) {
        return this._readFromLocal(key);
    }

    /**
     * @Description: 存储bool值
     * @Date: 2024-06-11 18:18:30
     * @Author: GFanStudio-LeeSir-JYJ add
     * @param {*} key
     * @param {*} value
     * @return {*}
     */
    public setBool(key, value) {
        let writeValue = (value === true ? "true" : "false");
        this._writeToLocal(key, writeValue);
    }

    /**
    * @Description: 获取bool值
    * @Date: 2024-06-11 18:18:30
    * @Author: GFanStudio-LeeSir-JYJ add
    * @param {*} key
    * @param {*} value
    * @return {*}
    */
    public getBool(key) {
        let value = this._readFromLocal(key);
        if (value == null || value == undefined) {
            return false;
        }
        return (value === "true");
    }

    private _writeToLocal(key, value) {
        if (value == null || value == undefined || value == "") {
            throw new Error(`[${TAG}] value is null or undefined`);
            return;
        }
        sys.localStorage.setItem(key, value);
    }

    private _readFromLocal(key) {
        return sys.localStorage.getItem(key);
    }

}

export let localDBUtils = GFLocalDBUtils.ins();


