import { _decorator, Component, Label, Node, NodeEventType, tween, Vec3 } from 'cc';
import { GameUtils } from '../../utils/GameUtils';
const { ccclass, property } = _decorator;

@ccclass('CommonPopView')
export class CommonPopView extends Component {

    @property(Label)
    titleLabel: Label = null;

    @property(Label)
    contentLabel: Label = null;

    @property(Node)
    maskNode: Node = null;

    @property(Node)
    closeBtnNode: Node = null;

    @property(Node)
    commonBtnNode: Node = null;

    @property(Label)
    commonBtnLabel: Label = null;

    private _closeCallback: Function = null;
    private _commonCallback: Function = null;

    private _titleStr: string = "";
    private _contentStr: string = "";
    private _btnDesStr: string = "";

    start() {
        this._initUIData();
    }

    private _initUIData() {
        if (this.maskNode) {
            this.maskNode.active = false;
            this.maskNode.on(NodeEventType.TOUCH_END, this.onClickCloseBtnEvent, this);
        }
        if (this.titleLabel) {
            this.titleLabel.node.active = false;
        }
    }

    public initWithData(contentStr: string, titleStr?: string, btnDesStr?: string, closeCallback?: Function, commonCallback?: Function) {

        if (!GameUtils.isCheckStringEmpty(contentStr)) {
            this._contentStr = contentStr;
            if (this.contentLabel) {
                this.contentLabel.string = contentStr;
            }
        }
        if (!GameUtils.isCheckStringEmpty(titleStr)) {
            this._titleStr = titleStr;
            if (this.titleLabel) {
                this.titleLabel.string = titleStr;
                this.titleLabel.node.active = true;
            }
        }

        if (!GameUtils.isCheckStringEmpty(btnDesStr)) {
            this._btnDesStr = btnDesStr;
            if (this.commonBtnLabel) {
                this.commonBtnLabel.string = btnDesStr;
            }
        }

        if (closeCallback) {
            this._closeCallback = closeCallback;
        }
        if (commonCallback) {
            this._commonCallback = commonCallback;
        }
        if (this.commonBtnNode) {
            this.commonBtnNode.active = (commonCallback) ? true : false;
        }

        tween(this.node).to(0.5, { scale: new Vec3(1, 1, 1) }, { easing: "bounceOut" }).call(() => {
            if (this.maskNode) {
                this.maskNode.active = true;
            }
        }).start();
    }

    /**
     * @Description: 通用按钮点击逻辑
     * @Date: 2024-06-09 22:14:08
     * @Author: GFanStudio-LeeSir-JYJ add
     * @return {*}
     */
    public onClickCommonBtnEvent() {
        if (this._commonCallback) {
            this._commonCallback();
        }
        this._closeSelf();
    }

    /**
     * @Description: 关闭界面逻辑
     * @Date: 2024-06-09 22:14:22
     * @Author: GFanStudio-LeeSir-JYJ add
     * @return {*}
     */
    public onClickCloseBtnEvent() {
        if (this._closeCallback) {
            this._closeCallback();
        }
        this._closeSelf();
    }

    /**
     * @Description: 自定义关闭函数
     * @Date: 2024-06-09 22:06:46
     * @Author: GFanStudio-LeeSir-JYJ add
     * @return {*}
     */
    private _closeSelf() {
        if (this.maskNode) {
            this.maskNode.active = false;
        }
        tween(this.node).to(0.1, { scale: new Vec3(0.1, 0.1, 0.1) }).call(() => {
            this.node.active = false;
            this.node.removeFromParent();
            this.node.destroy();
        }).start();
    }

    protected onDestroy(): void {
        this.node.destroy();
    }

    update(deltaTime: number) {

    }
}

