/**
 * @Description: 大炮配置
 * @Date: 2024-06-16 19:02:42
 * @Author: GFanStudio-LeeSir-JYJ add
 * @return {*}
 */
export interface ICannonConfigsObj {
    cannonType: number,//大炮类型
    typeIndex: number,//大炮数组下标
    needGolds: number,//每使用一次此大炮需要消耗的金币数
    hurtHp: number,//每打中一次鱼掉的血量
    lockGolds: number,//解锁此种大炮需要的金币数量
    lockLevel: number,//默认游戏等级达到多少级才解锁大炮
    name: string,
    desc: string
}

/**
 * @Description: 鱼群配置
 * @Date: 2024-06-16 19:02:32
 * @Author: GFanStudio-LeeSir-JYJ add
 * @return {*}
 */
export interface IFishConfigsObj {
    fishType: string,//鱼类型名
    type: number,//鱼所属类型
    hp: number,//鱼的血量
    golds: number,//打死可获得的银币/金币数量
    lockGolds: number,//解锁此种鱼需要的金币数量
    lockLevel: number,//默认游戏等级达到多少级才解锁鱼
    name: string,
    desc: string,
    scale: number //鱼缩放比例
    speed: number//鱼默认游动速度
}

/**
 * @Description: 商店配置
 * @Date: 2024-06-16 19:02:49
 * @Author: GFanStudio-LeeSir-JYJ add
 * @return {*}
 */
export interface IShopConfigsObj {
    itemId: number,
    num: number,
    golds: number,
    name: string,
    desc: string,
    icon: string
}

/**
 * @Description: 主题背景配置相关 涉及游戏内部等级和分数转换
 * @Date: 2024-06-16 19:02:58
 * @Author: GFanStudio-LeeSir-JYJ add
 * @return {*}
 */
export interface IThemeConfigsObj {
    bgId: number,
    min_score: number,
    max_score: number,
    level: number,
    bg_name: string,
}