import { _decorator, Component, Node, Size, Tween, Vec3, view } from 'cc';
import { GameWeaponLevelType } from '../../common/GameConst';
import { BulletNodePool } from '../../pools/BulletNodePool';
const { ccclass, property } = _decorator;

@ccclass('BulletItem')
export class BulletItem extends Component {

    @property([Node])
    bulletNodeArr: Node[] = [];

    private _curWeaponLevelType: number = 1;
    
    private _visibleSize: Size = new Size(960, 640);
    private _shotMoveSpeed: number = 500;
    private _curAngle: number = 0;
    private _bulletID:number = 0; //子弹ID
    start() {

    }

    public initBulletWithData(weaponLevelType: number,bulletID:number) {
        this._curWeaponLevelType = weaponLevelType;
        this._visibleSize = view.getVisibleSize();
        this._updateBulletItemByLevelType(this._curWeaponLevelType);
        this._curAngle = -this.node.angle;
        this._bulletID = bulletID;

    }
    public get bulletID():number{
        return this._bulletID;
    }
    public get curWeaponLevelType() {
        return this._curWeaponLevelType;
    }
    /**
     * @Description: 根据武器等级类型切换子弹的类型
     * @Date: 2024-06-13 08:10:55
     * @Author: GFanStudio-LeeSir-JYJ add
     * @param {number} weaponLevelType
     * @return {*}
     */
    private _updateBulletItemByLevelType(weaponLevelType: number) {
        this.node.active = true;
        if (this.bulletNodeArr.length <= 0) {
            this._curWeaponLevelType = Number(GameWeaponLevelType.GWL_LevelType01);
        }
        if (weaponLevelType > 7) {
            this._curWeaponLevelType = Number(GameWeaponLevelType.GWL_LevelType07);
        } else if (weaponLevelType < 1) {
            this._curWeaponLevelType = Number(GameWeaponLevelType.GWL_LevelType01);
        }
        for (let i = 0; i < this.bulletNodeArr.length; i++) {
            const node = this.bulletNodeArr[i];
            if (!node) {
                continue;
            }
            let index = (i + 1);
            if (this._curWeaponLevelType == index) {
                node.active = true;
            } else {
                node.active = false;
            }
        }
    }

    /**
     * @Description: 更新子弹位置
     * @Date: 2024-06-13 08:12:20
     * @Author: GFanStudio-LeeSir-JYJ add
     * @return {*}
     */
    private _updateBulletPos(dt: number) {
        if (!this.node) {
            return;
        }
        let posX = this.node.position.x;
        let posY = this.node.position.y;
        posX += (dt * this._shotMoveSpeed * Math.sin(-this.node.angle * Math.PI / 180));
        posY += (dt * this._shotMoveSpeed * Math.cos(-this.node.angle * Math.PI / 180));
        this.node.setPosition(new Vec3(posX, posY));
        if (posX > this._visibleSize.width / 2 + 100 ||
            posX < -this._visibleSize.width / 2 - 100 ||
            posY > this._visibleSize.height / 2 + 100 ||
            posY < -this._visibleSize.height / 2 - 100) {

            Tween.stopAllByTarget(this.node);
            this.node.active = false;
            this.node.removeFromParent();
            this.node.parent = null;
            // this.node.destroy();
            BulletNodePool.put(this.node);
        }
    }

    update(deltaTime: number) {

        this._updateBulletPos(deltaTime);

    }
}

