import { gameData } from "../../game/common/GameData";
import { GFSingleClass } from "../base/GFSingleClass";
import { CryptoUtils } from "../crypto/CryptoUtils";
import { eventMgr } from "../manager/EventManager";
import { WSNetCmd, WSNetCmdType, getGameProtoIdByWsNetCmd } from "./WSNetCmd";

enum NetState {
    DisConnect = 1,//未连接
    Connecting,//连接中
    Connected,//已连接
}

export interface INetMessage {
    protoid: WSNetCmd,//网络请求指令
    cmd: string,
    msg: any
}

let TAG = 'WSManager';
class WSManager extends GFSingleClass {

    private static _netState: NetState = NetState.DisConnect;
    private static _ws: WebSocket = null;
    private static _responseMsgQueue: INetMessage[] = [];//消息队列
    private static _isChangeScene: boolean = false;//标记当前是否正在切换场景过程中

    public static ins() {
        return super.ins() as WSManager;
    }

    public static setChangeScene(isChangeScene: boolean) {
        this._isChangeScene = isChangeScene;
    }

    public static wsConnect(url: string) {

        if (this._netState != NetState.DisConnect) {
            return;
        }
        WSManager._netState = NetState.Connecting;
        WSManager._ws = new WebSocket(url);
        WSManager._ws.binaryType = 'arraybuffer';

        WSManager._ws.onopen = () => {
            this._netState = NetState.Connected;
            //根据自身游戏逻辑需求而定
            let uid = gameData.curPlayerData.uid;
            let uname = gameData.curPlayerData.uname;
            let normalUName = gameData.curPlayerData.normalUName;
            let password = gameData.curPlayerData.password;
            //根据自身游戏逻辑需求而定-检测校验链接是否成功
            this.sendMessage(WSNetCmd.WNC_CheckConnect, { uid: uid, uname: uname, password: password, normalUName: normalUName });
        }

        WSManager._ws.onmessage = (event) => {
            const arraybuffer = event.data;
            let dataUnit8Array = new Uint8Array(arraybuffer);
            let bufferDataStr: string = this._arrayBufferToString(dataUnit8Array);
            // let bufferDataObj = JSON.parse(bufferDataStr) as INetMessage;
            //解密
            let deCodeBufferDataStr = CryptoUtils.decrypt(bufferDataStr);//解密方法1
            let bufferDataObj = JSON.parse(deCodeBufferDataStr) as INetMessage;
            if (!bufferDataObj) {
                return;
            }
            this._responseMsgQueue.push(bufferDataObj);
            this._processNetMessageEmit();
        }

        WSManager._ws.onclose = () => {
            if (this._netState === NetState.Connected && this._ws != null) {
                this._ws.onopen = () => { };
                this._ws.onmessage = () => { };
                this._ws.onclose = () => { };
                this._ws.onerror = () => { };
                this._ws.close();
                this._ws = null;
            }
            this._netState = NetState.DisConnect;
            //根据情况和服务器进行一次断链的消息通知
        }

        WSManager._ws.onerror = (error) => {

            console.log(TAG, ` error :${error}`);

        }
    }

    /**
     * @Description: 全局发送消息到服务器方法
     * @Date: 2024-04-24 21:50:54
     * @Author: GFanStudio-LeeSir-JYJ add
     * @param {WSNetCmd} protoid 自定义消息id
     * @param {any} sendData 传输的json数据结构{}
     * @return {*}
     */
    public static sendMessage(protoid: WSNetCmd, sendData: any) {
        if (this._netState != NetState.Connected || !this._ws) {
            return;
        }
        let cmdStr = getGameProtoIdByWsNetCmd(protoid, WSNetCmdType.WNCT_Request);
        let msgDataJsonObj = {
            protoid: protoid,
            cmd: cmdStr,
            msg: sendData
        };
        let jsonStr = JSON.stringify(msgDataJsonObj);
        // let arrBuff = this._stringToUint8Array(jsonStr);
        //加密数据
        let encodeJsonStr = CryptoUtils.encrypt(jsonStr);//加密方法1
        let arrBuff = this._stringToUint8Array(encodeJsonStr);
        this._ws.send(arrBuff);
    }

    /**
     * @Description: 处理消息队列
     * @Date: 2024-04-24 21:25:42
     * @Author: GFanStudio-LeeSir-JYJ add
     * @return {*}
     */
    private static _processNetMessageEmit() {

        if (this._responseMsgQueue.length <= 0) {
            return;
        }
        if (this._isChangeScene) {
            return;
        }
        while (1) {
            let packet = this._responseMsgQueue.shift();
            console.log(TAG, `<handle server msg> packet : ${JSON.stringify(packet)}`);
            let protoid: WSNetCmd = packet.protoid as WSNetCmd;
            let cmd: string = packet.cmd as string;
            let cmdStr: string = getGameProtoIdByWsNetCmd(protoid, WSNetCmdType.WNCT_Response);
            if (cmd != cmdStr) {
                break;
            }
            let msgObj = packet.msg;
            //分发消息到各自监听的界面
            eventMgr.emit(cmdStr, msgObj);
            if (this._responseMsgQueue.length <= 0) {
                break;
            }
        }

    }

    /**
     * @Description:字符串转字节数组 
     * @Date: 2024-04-24 22:18:50
     * @Author: GFanStudio-LeeSir-JYJ add
     * @param {string} jsonStr
     * @param {*} option
     * @return {*}
     */
    private static _stringToUint8Array(jsonStr: string, option = { steam: false }): Uint8Array {

        if (option.steam) {
            throw new Error(`failed to encode: the 'steam' option is unsupported.`);
        }

        let pos = 0;
        const len = jsonStr.length;
        const out = [];
        let at = 0;
        let tlen = Math.max(32, len + (len >> 1) + 7);//1.5x size
        let target = new Uint8Array((tlen >> 3) << 3);//做一个8字节的偏移

        while (pos < len) {
            let value = jsonStr.charCodeAt(pos++);
            if (value > 0xd800 && value <= 0xdc00) {
                if (pos < len) {
                    const extra = jsonStr.charCodeAt(pos);
                    if ((extra & 0xfc00) === 0xdc00) {
                        ++pos;
                        value = ((value & 0x3ff) << 10) + (extra & 0x3ff) + 0x10000;
                    }
                }
                if (value >= 0xd800 && value <= 0xdbff) {
                    continue;
                }
            }
            //如果说不能写入4字节追加字节
            if (at + 4 > target.length) {
                tlen += 8;
                tlen *= (1.0 + (pos / jsonStr.length) * 2);
                tlen = (tlen >> 3) << 3;//做一个8字节的偏移

                const newArr = new Uint8Array(tlen);
                newArr.set(target);
                target = newArr;
            }

            if ((value & 0xffffff80) === 0) {//1个字节
                target[at++] = value;//赋值ASCII
                continue;
            } else if ((value & 0xffffff800) === 0) {//2个字节
                target[at++] = ((value >> 6) & 0x1f) | 0xc0;
            } else if ((value & 0xffff000) === 0) {//3个字节
                target[at++] = ((value >> 12) & 0x0f) | 0xe0;
                target[at++] = ((value >> 6) & 0x3f) | 0x80;
            } else if ((value & 0xffe00000) === 0) {//4个字节
                target[at++] = ((value >> 18) & 0x07) | 0xf0;
                target[at++] = ((value >> 12) & 0x3f) | 0x80;
                target[at++] = ((value >> 6) & 0x3f) | 0x80;
            } else {
                continue;
            }
            target[at++] = (value & 0x3f) | 0x80;
        }
        return target.slice(0, at);
    }

    /**
     * @Description: 将字节数组转换为字符串
     * @Date: 2024-04-24 21:20:43
     * @Author: GFanStudio-LeeSir-JYJ add
     * @param {Uint8Array} arrBuff
     * @return {*}
     */
    private static _arrayBufferToString(arrBuff: Uint8Array): string {

        if (typeof arrBuff === 'string') {
            return arrBuff;
        }
        let finalStr: string = '';
        let _tempArrBuff = arrBuff;
        for (let i = 0; i < _tempArrBuff.length; i++) {
            let one = _tempArrBuff[i].toString(2);
            let v = one.match(/^1+?(?=0)/);
            if (v && one.length == 8) {
                let bytesLen = v[0].length;
                let store = _tempArrBuff[i].toString(2).slice(7 - bytesLen);
                for (let st = 1; st < bytesLen; st++) {
                    store += _tempArrBuff[st + i].toString(2).slice(2);
                }
                finalStr += String.fromCharCode(parseInt(store, 2));
                i += bytesLen - 1;
            } else {
                finalStr += String.fromCharCode(_tempArrBuff[i]);
            }
        }
        return finalStr;
    }


}

let wsMgr = WSManager.ins();
export { wsMgr, WSManager }