import { _decorator, Animation, Component, Label, Node } from 'cc';
const { ccclass, property } = _decorator;

@ccclass('FlyCoinsItem')
export class FlyCoinsItem extends Component {

    @property(Node)
    aniNode: Node = null;

    @property(Animation)
    coinGoldAni: Animation = null;

    @property(Animation)
    coinSilverAni: Animation = null;

    @property(Label)
    coinNumLabel: Label = null;

    start() {

    }

    public initFlyCoinWithData(coinNum: number, isShowGold: boolean = true) {
        if (!this.aniNode) {
            return;
        }
        if (this.coinGoldAni) {
            this.coinGoldAni.node.active = isShowGold;
        }
        if (this.coinSilverAni) {
            this.coinSilverAni.node.active = !isShowGold;
        }
        this.scheduleOnce(() => {
            if (this.coinGoldAni) {
                if (isShowGold) {
                    this.coinGoldAni.play();
                }
            }
            if (this.coinSilverAni) {
                if (!isShowGold) {
                    this.coinSilverAni.play();
                }
            }
        }, 0.1);
        if (this.coinNumLabel) {
            this.coinNumLabel.string = `+${coinNum}`;
        }

    }

    update(deltaTime: number) {

    }
}

