import { _decorator, BoxCollider2D, Component, Node, screen, Size, Sprite, SpriteFrame, UITransform, Vec3, view, Animation, FixedConstraint, AnimationState, Tween, Vec2, director } from 'cc';
import { IFishConfigsObj } from '../../common/GameInterface';
import { FishLifeState, FishTypeGroup } from '../../common/GameConst';
import { gameData } from '../../common/GameData';
import { GameUtils } from '../../utils/GameUtils';
import { loaderMgr } from '../../managers/LoaderManager';
import { GameResPath } from '../../common/GameResPath';
import { FishNodePool } from '../../pools/FishNodePool';
import { GameEvents } from '../../common/GameEvents';
import { gameMgr } from '../../gameMgr';
import { FishGameMgr } from '../../FishGameMgr';
import { gameNet } from 'db://assets/module_basic/scripts/NetGameServer';
const { ccclass, property } = _decorator;

let TAG = 'FishItem';

@ccclass('FishItem')
export class FishItem extends Component {

    //当前鱼类的血条值
    private _curHPValue: number = 10;
    //当前鱼的状态
    private _curFishLifeState: FishLifeState = FishLifeState.FLS_WaitReBirth;


    //保存上一次移动坐标,用于更新鱼角度
    private _lastPosition: Vec3 = Vec3.ZERO;
    //当前鱼身上的属性配置
    private _curFishCfgObj: IFishConfigsObj = null;
 

    private _curFishTypeName: string = "";
    private _curVisibleSize: Size = new Size(960, 640);
    private _curFishParentNode: Node = null;
    private _curFishTypeGroup: number;

    private _fishScalValue: number = 1;
    private _fishBaseSwimSpeed: number = 20;
    private _fishID:number = 0;

    start() {

    }
    public get fishID():number
    {
        return this._fishID;
    }
    public get curFishCfgObj() {
        return this._curFishCfgObj;
    }
    public get curFishTypeGroup() {
        return this._curFishTypeGroup;
    }
    public get curFishLifeState() {
        return this._curFishLifeState;
    }
    public set curFishLifeState(value: FishLifeState) {
        this._curFishLifeState = value;
    }
    public get curHPValue() {
        return this._curHPValue;
    }
    public set curHPValue(value: FishLifeState) {
        this._curHPValue = value;
    }

    /**
     * @Description: 初始化生成鱼类
     * @Date: 2024-06-13 17:28:29
     * @Author: GFanStudio-LeeSir-JYJ add
     * @param {Node} parentNode
     * @param {string} fishTypeName
     * @return {*}
     */
    public initFishWithData(parentNode: Node, fishID:number,fishGroupType: number) {
        this._fishID = fishID;
        this._curFishTypeGroup = fishGroupType;
        if (!parentNode) {
            return;
        }
        this.node.setScale(this._fishScalValue, this._fishScalValue);
        
        this._curFishParentNode = parentNode;
        let fishCgsLen: number = gameData.fishConfigsObjArr.length;
        if (fishCgsLen <= 0) {
            return;
        }
        //fishCgsLen = gameData.curPlayerData.lock_fishtype.length;
        // if (fishGroupType == FishTypeGroup.FTC_NormalFish) {
        //     //随机生成普通鱼类
        //     // let randomFishIndex = GameUtils.recursionRandomFunc(fishCgsLen, [9, 10]);
        //     //过滤掉蓝鲨和金鲨
        //     let filterNewTypeArr = gameData.curPlayerData.lock_fishtype.filter((type) => {
        //         if (type != 10 && type != 11) {
        //             return true;
        //         }
        //         return false;
        //     });
        //     let randomFishIndex = Math.floor(Math.random() * filterNewTypeArr.length);
        //     // console.log(TAG, `filterNewTypeArr: ${filterNewTypeArr}  , randomFishIndex: ${randomFishIndex}`);
        //     let type = gameData.curPlayerData.lock_fishtype[randomFishIndex];//1-11类型
        //     this._curFishCfgObj = gameData.fishConfigsObjArr[(type - 1)];
        // } else {
        //     //随机生成鲨鱼
        //     let fishTypeName = gameData.getSharkTypeNameByGroupType(fishGroupType);
        //     this._curFishCfgObj = gameData.getFishConfigObjByFishTypeName(fishTypeName);
        // }

        // let type = gameData.curPlayerData.lock_fishtype[fishGroupType-1];//1-11类型
        this._curFishCfgObj = gameData.fishConfigsObjArr[(fishGroupType - 1)];
        if (!this._curFishCfgObj) {
            return;
        }
        this._fishBaseSwimSpeed = this.curFishCfgObj.speed;
        this._fishScalValue = this.curFishCfgObj.scale;
        this._curFishTypeName = this._curFishCfgObj.fishType;
        this._curHPValue = this.curFishCfgObj.hp;
        this._curFishLifeState = FishLifeState.FLS_Alive;
        //随机初始位置相关逻辑
        this._curVisibleSize = view.getVisibleSize();
        let randomPos = new Vec3(-Math.random() * 20 - 80 - this._curVisibleSize.width / 2, (Math.random() - 0.5 * 2 * 300 + this._curVisibleSize.height / 2));
        this.node.position = randomPos;
        //随机取出鱼要出现的位置
        let bezierIndex = Math.floor(Math.random() * gameData.traceConfigsObjArr.length);
        let bezierPathArr = gameData.traceConfigsObjArr[bezierIndex];
        let startPos = bezierPathArr[0];
        //弧度和角度转换
        let degreeValue = Math.atan(startPos.y / startPos.x);
        this.node.angle = -degreeValue * 180 / Math.PI;
        this.node.active = false;
        //console.log(TAG, `fishTypeName: ${this._curFishTypeName}`);
        loaderMgr.loadGameRes(GameResPath.getFishSpFramePath(this._curFishTypeName, true), SpriteFrame)
            .then((spriteFrame: SpriteFrame) => {
                let spComp = this.node.getComponent(Sprite);
                if (spComp) {
                    this.node.setScale(this._fishScalValue, this._fishScalValue);
                    spComp.spriteFrame = spriteFrame;
                    this._updateCurNodeBoxCollider(this.node);
                    this.scheduleOnce(() => {
                        this.node.active = true;
                        this.playSwimmingAction();
                    }, 1);
                    this.node.parent = this._curFishParentNode;
                    this._lastPosition = this.node.position;
                    this.playFishSwimmingBezierAction(bezierPathArr);
                }
            })
            .catch((err) => {
                console.error(TAG, `<initFishWithData> err : ${err}`);
            });
    }

    /**
     * @Description: 游动动画
     * @Date: 2024-06-13 21:16:44
     * @Author: GFanStudio-LeeSir-JYJ add
     * @return {*}
     */
    public playSwimmingAction() {
        this._playSwimmingActoin(this._curFishTypeName, FishLifeState.FLS_Alive);
    }

    /**
     * @Description:死亡动画 
     * @Date: 2024-06-13 21:16:51
     * @Author: GFanStudio-LeeSir-JYJ add
     * @return {*}
     */
    public playDieAction() {
        this._playSwimmingActoin(this._curFishTypeName, FishLifeState.FLS_Dead);
    }

    /**
     * @Description: 让鱼沿着贝塞尔曲线游动
     * @Date: 2024-06-13 21:38:05
     * @Author: GFanStudio-LeeSir-JYJ add
     * @param {Vec3} bezierTracePathArr
     * @return {*}
     */
    public playFishSwimmingBezierAction(bezierTracePathArr: Vec3[]) {
        if (!this.node || bezierTracePathArr.length < 3) {
            return;
        }
        Tween.stopAllByTarget(this.node);
        // let speed = Math.random() * 10 + 20;
        let speed = Math.random() * 6 + this._fishBaseSwimSpeed;
        GameUtils.qtCustomBezierToAction(
            this.node,
            speed,
            bezierTracePathArr[0],
            bezierTracePathArr[1],
            bezierTracePathArr[2],
            "quadIn",
            {
                onComplete: (target: Node) => {

                }
            }, this._fishScalValue).start();
    }

    /**
     * @Description: 更新设置boxCollider的size
     * @Date: 2024-06-13 18:24:36
     * @Author: GFanStudio-LeeSir-JYJ add
     * @param {Node} fishNode
     * @return {*}
     */
    private _updateCurNodeBoxCollider(fishNode: Node) {
        if (!this.node || !fishNode) {
            return;
        }
        let fishAniNodeUITrans = fishNode.getComponent(UITransform);
        if (fishAniNodeUITrans) {
            let curBoxColliderComp = this.node.getComponent(BoxCollider2D);
            if (curBoxColliderComp) {
                curBoxColliderComp.size = fishAniNodeUITrans.contentSize;
            }
        }

    }

    /**
     * @Description: 播放鱼的游动和死亡动画
     * @Date: 2024-06-13 21:12:42
     * @Author: GFanStudio-LeeSir-JYJ add
     * @param {string} fishTypeName
     * @param {FishLifeState} fishLifeState
     * @return {*}
     */
    private _playSwimmingActoin(fishTypeName: string, fishLifeState: FishLifeState) {
        let fishAniComp = this.node.getComponent(Animation);
        if (fishAniComp) {
            fishAniComp.stop();
            if (fishLifeState === FishLifeState.FLS_Alive) {
                fishAniComp.play(`${fishTypeName}_run`);
            } else if (fishLifeState === FishLifeState.FLS_Dead) {
                fishAniComp.play(`${fishTypeName}_dead`);
                fishAniComp.on(Animation.EventType.FINISHED, this._onFishAnimationFinishCallback, this);
            }
        }
    }

    /**
     * @Description: 播放放死亡动画结束监听
     * @Date: 2024-06-13 21:14:32
     * @Author: GFanStudio-LeeSir-JYJ add
     * @param {Animation} type
     * @param {AnimationState} state
     * @return {*}
     */
    private _onFishAnimationFinishCallback(type: Animation.EventType, state: AnimationState) {
        this._removeDestroyNode();
    }

    /**
     * @Description: 实时更新鱼的角度
     * @Date: 2024-06-13 21:49:21
     * @Author: GFanStudio-LeeSir-JYJ add
     * @return {*}
     */
    private _updateFishDegree() {
        if (!this.node) {
            return;
        }
        let currentPos = this.node.position;
        let tartgetPos = this._lastPosition;
        let dx = tartgetPos.x - currentPos.x;
        let dy = tartgetPos.x - currentPos.y;
        let dir = new Vec2(dx, dy);
        let angle = dir.signAngle(new Vec2(1, 0));
        this.node.angle = angle;
        this._lastPosition = currentPos;
    }

    private _checkFishIsAlive() {
        if (this._curFishLifeState === FishLifeState.FLS_Alive) {
            return true;
        }
        return false;
    }

    private _checkFishIsDie() {
        if (this._curFishLifeState === FishLifeState.FLS_Dead) {
            return true;
        }
        return false;
    }

    private _destroyRecyclingFish() {
        if (!this.node) {
            return;
        }
        let posX = this.node.position.x;
        let posY = this.node.position.y;
        if (posX > this._curVisibleSize.width / 2 + 150
            || posX < -this._curVisibleSize.width / 2 - 900
            || posY > this._curVisibleSize.height / 2 + 100
            || posY < -this._curVisibleSize.height / 2 - 100
        ) {
            // console.log(TAG, `<_destroyRecyclingFish>`);
            this._removeDestroyNode();
        }
    }

    public _removeDestroyNode() {
        if (!this.node) {
            return;
        }
        if(!this.node.active)
        {
            return;
        }

        director.emit(GameEvents.UI_FishRemove, {
            id:this.fishID
        });
        Tween.stopAllByTarget(this.node);
        this.node.active = false;
        let spComp = this.node.getComponent(Sprite);
        if (spComp) {
            spComp.spriteFrame = null;
        }
        this.node.removeFromParent();
        this.node.parent = null;
        this.node.setScale(this._fishScalValue, this._fishScalValue);
        // FishNodePool.put(this.node);
        this.node.destroy();
    }

    update(deltaTime: number) {
        if (this._checkFishIsAlive()) {
            this._updateFishDegree();
            this._destroyRecyclingFish();
        } else {
            if (this._checkFishIsDie()) {
                this._curFishLifeState = FishLifeState.FLS_WaitReBirth;
                Tween.stopAllByTarget(this.node);
                this.playDieAction();
                //TODO 执行播放计分、飞金币等逻辑
            }
        }
    }


}

